import React from 'react';
import './App.css';



class WatchList extends React.Component {
  constructor(props) {
    super(props);
  }



bringRusultsComingTodayInView=()=>{
      let today = new Date().toISOString().split('T')[0];
      let selector = "tr[rowid='RESULT-DATE-"+today+"']";
      if(document.querySelectorAll(selector) && document.querySelectorAll(selector)[0]){
        document.querySelectorAll(selector)[0].scrollIntoView();  
      }     
}


  render() {

    const{
      kitedata,
      symbolList,
      kitePerfData,
      onTableRowclick,
      tableId,
      showToast,
      resultCalenderData,
      recentResultCalender,
      kiteBasketOrderData
    }=this.props;

    let tempSymbols=[];
    if(kitedata){
       tempSymbols = symbolList.filter(function(symbol){ return (symbol == 'SEPERATOR' || kitedata.ticks[symbol]!=undefined) });
    }




    return (
          <table>
            <thead>
              <tr>
                <th>Name</th>
                <th>Change</th>
                <th>LTP</th>
              </tr>
            </thead>

            <tbody>
            
             { tempSymbols.map((symbol,index)=> <WatchListRow showToast={showToast} tableId={tableId} onTableRowclick={onTableRowclick} key={symbol+index} kitedata={kitedata} kitePerfData={kitePerfData} symbol={symbol} />)}
            
            { resultCalenderData && <React.Fragment >
               <tr></tr>
               <tr></tr>
               <tr onClick={this.bringRusultsComingTodayInView} rowId={"RES-RESCALENDER"}><td colSpan="3" style={{textAlign:'center',fontWeight:'bold'}}>RES CALENDER</td></tr> 
               { resultCalenderData.map((symbol,index)=> <WatchListRow showToast={showToast} tableId={'RES'} onTableRowclick={onTableRowclick} key={'RES-'+index+symbol} kitedata={kitedata} kitePerfData={kitePerfData} symbol={symbol} />)}
               
               <tr><td colSpan="3" style={{textAlign:'center'}}>UPCOMMING RESULTS</td></tr>
               {
                Object.keys(recentResultCalender).map((key,keyindex)=>{
                  return <React.Fragment key={key} ><tr rowId={'RESULT-DATE-'+key} key={key+keyindex}><td colSpan="3" className="resultDate">{new Date(key).toDateString()}</td></tr>
                  {recentResultCalender[key].map((data,index)=> <WatchListRow showToast={showToast}  onTableRowclick={onTableRowclick} key={'UPCOMMING-RES-'+index+data.symbol} kitedata={kitedata} kitePerfData={kitePerfData} symbol={data.symbol} inHolding={data.inHolding}/>)}
                </React.Fragment>
                })
               }

                
             </React.Fragment>}


            { kiteBasketOrderData && <React.Fragment >
               <tr></tr>
               <tr></tr>
               <tr rowId={"MIS-MISSCRIPTS"} ><td colSpan="3" style={{textAlign:'center',fontWeight:'bold'}}>MIS SCRIPTS</td></tr> 
               { kiteBasketOrderData.map((symbol,index)=> <WatchListRow showToast={showToast} tableId={'MIS'} onTableRowclick={onTableRowclick} key={'MIS-'+index+symbol} kitedata={kitedata} kitePerfData={kitePerfData} symbol={symbol} />)}
             </React.Fragment>}


            </tbody>


        </table>
    )
  }

}


export default WatchList;

class WatchListRow extends React.Component {
  constructor(props) {
    super(props);
  }


  componentDidUpdate(prevProps) {
    let symbol = this.props.symbol;
     if (symbol!= 'SEPERATOR' && prevProps.symbol == symbol ) {
       if(prevProps.kitedata.ticks[symbol].change < 3 && this.props.kitedata.ticks[symbol].change > 3
        || prevProps.kitedata.ticks[symbol].change < 5 && this.props.kitedata.ticks[symbol].change > 5
        || prevProps.kitedata.ticks[symbol].change < 8 && this.props.kitedata.ticks[symbol].change > 8
        || prevProps.kitedata.ticks[symbol].change < 9.5 && this.props.kitedata.ticks[symbol].change > 9.5
        || prevProps.kitedata.ticks[symbol].change < 13 && this.props.kitedata.ticks[symbol].change > 13
        || prevProps.kitedata.ticks[symbol].change < 15 && this.props.kitedata.ticks[symbol].change > 15
        ){

        this.props.showToast(<div onClick={this.openStockInfo} className='green'>{this.props.symbol +"  ->  " + (this.props.kitedata.ticks[symbol].lastTradedPrice)}&nbsp;(<span>{(this.props.kitedata.ticks[symbol].change).toFixed(2)}</span>%)</div>,this.props.symbol);

       }


       if(prevProps.kitedata.ticks[symbol].change > -3 && this.props.kitedata.ticks[symbol].change < -3
        || prevProps.kitedata.ticks[symbol].change > -5 && this.props.kitedata.ticks[symbol].change < -5
        || prevProps.kitedata.ticks[symbol].change > -8 && this.props.kitedata.ticks[symbol].change < -8
        || prevProps.kitedata.ticks[symbol].change > -9.5 && this.props.kitedata.ticks[symbol].change < -9.5
        || prevProps.kitedata.ticks[symbol].change > -13 && this.props.kitedata.ticks[symbol].change < -13
        || prevProps.kitedata.ticks[symbol].change > -15 && this.props.kitedata.ticks[symbol].change < -15
        ){

        this.props.showToast(<div onClick={this.openStockInfo} className='red'>{this.props.symbol +"  ->  " + (this.props.kitedata.ticks[symbol].lastTradedPrice)}&nbsp;(<span>{(this.props.kitedata.ticks[symbol].change).toFixed(2)}</span>%)</div>,this.props.symbol);

       }


     }



  }


  openStockInfo=()=>{
    this.props.onTableRowclick(this.props.symbol,this.props.tableId);
  }


  render() {

    const{
      kitedata,
      symbol,
      kitePerfData,
      onTableRowclick,
      tableId,
      inHolding //use for result calender
    }=this.props;

    let isSeperator = (symbol =='SEPERATOR')?true:false;

    let changeClass  = '';
    if( kitedata.ticks[symbol] ){
      changeClass=kitedata.ticks[symbol].change<0 ?'red':'green';
    }
    
    let prefStyle = {};
    if(kitePerfData && kitePerfData[symbol]){
      prefStyle.backgroundColor=kitePerfData[symbol].color;
    }
    if(inHolding==true){
      prefStyle.backgroundColor = 'lightgray';
    }

    return (
      <tr onClick={this.openStockInfo} rowId={tableId?(tableId+"-"+symbol):symbol}>
        {!isSeperator &&   <td style={prefStyle}>{kitedata.ticks[symbol].name}</td> }
        {!isSeperator &&   <td className={changeClass} >{(kitedata.ticks[symbol].change).toFixed(2)}</td> }
        {!isSeperator &&   <td>{kitedata.ticks[symbol].lastTradedPrice}</td> }              
       </tr>
    );


  }

}