const fillCNCLimitOrderDetails=(data)=>{
	data.order_type = 'LIMIT';
	data.product = 'CNC';
	data.validity = 'DAY';
	data.disclosed_quantity = 0;
	data.trigger_price = 0;
	data.squareoff = 0;
	data.stoploss = 0;
	data.trailing_stoploss = 0;
	data.variety = 'regular';
	data.user_id = 'DA6170';
}


